const initialState = {
  product: null, // Sn phm hin ti s l null khi khng c Popup
};

const ReducerPopup = (state = initialState, action) => {
  switch (action.type) {
    case 'SHOW_POPUP':
      return {
        ...state,
        product: action.payload, // Cp nht thng tin sn phm trong state
      };
    case 'HIDE_POPUP':
      return {
        ...state,
        product: null, // n Popup bng cch t sn phm thnh null
      };
    default:
      return state;
  }
};

export default ReducerPopup;
